body {
  margin: 0;
  padding: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: auto !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* Card Search */
.CardSearchWrapper{
  margin-top:1vh;
  margin-bottom:1vh;

}
.searchResultsCard{
  margin-top:1vh;
  padding-left:0.5vh !important;
  padding-right:0.5vh !important;

}
.searchResultsCard Button{
  padding: .5vh .5vh .5vh .5vh;
  border-radius: 0;
  background-color: black;
}
.legalButton{
  margin-top:1vh;
  border-radius: 0 !important;
  width:100%
}

.modal-content{
  border-radius: 0;
}

/* Planechase */
.PlanechaseWrapper{
  margin-top:1vh;
  margin-bottom:1vh;
}
.cardButton{
  padding: 1vh 1vh 1vh 1vh;
  margin-top: 1vh;
  background-color: black;
}
.planeCardWrapper Button{
  background-color: black;
  border-radius: 0;
}
.planeCard{
  width: 70%;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  margin-top:-15%;
  margin-bottom:-15%;
}
.planeName{
  font-size:1rem;
  font-weight: 500;
  color:#e2e2e2;
  margin-bottom:0;
}
.addAllButton{
  height:9vh;
  border-radius: 0;
  font-weight: 500;
  width:100%;
  margin-bottom: 1vh;

}
.clearAllButton{
  height:9vh;
  border-radius: 0;
  font-weight: 500;
  width:100%;
  margin-bottom: 1vh;


}
.startGameButton{
  height:9vh;
  border-radius: 0;
  font-weight: 500;
  width:100%;

}
.endGameButton{
  height:9vh;
  border-radius: 0;
  font-weight: 500;
  margin-top:1vh;
  width:100%;
}
.gameDeck{
  overflow-y: scroll;
  margin-bottom: 1vh;
  height: 29vh;
  max-height: 29vh;
  border-style: solid;
  border-width: 1px;
  border-color:#6c757d;
  width:100%;
  box-shadow: 2px 2px 2px grey;
}
.deckCard{
  font-size:1rem;
  text-align: left;
  margin-bottom:0.5rem;
  padding-left:1rem;
}
.removeButton{
  margin-right:0.5rem;
  cursor: pointer;

}
.currentCard{
  cursor: pointer;
}
/* Battle Counter */
.BattleCounterWrapper{
  margin-top:1vh;
}
.playerName{
  border-style: none;
  text-align: center;
  width:100%;
  background-color: transparent !important;
}
.playerCard{
  margin-top:1vh;
  border-radius: 0;
}
.counterNumber{
  font-size: 1.5rem;
  font-weight:500;
  margin:0;
}
.plusContainer{
  text-align: right;
}
.minusContainer{
  text-align: left;
}

/* Home Page */
.homeWrapper{
  margin-top:1vh;
}
.homeText{
  font-size: 1.25rem;
  font-weight: 500;
  margin-top:1vh;
  margin-bottom:1vh;
}
.homeCard{
  padding-top:0 !important;
  padding-bottom:0 !important;
  padding-left:0 !important;
  padding-right:0 !important;
  border-radius: 0 !important;
  width:100%
}
.homeImage{
  width:100%
}

/* Set Search */
.setsTable{
  text-align: left;
}
.setIcon{
  height:1rem;
  text-align: left;
}
.tokenSet{
  padding-left:2rem;
}
.setRow{
  cursor: pointer;
}
.loadingImage{
  width:50%;
  margin-top:30vh;
}
.returnButton{
  width:100%;
  margin-top:1vh;
}
/* General */
.manaSymbol{
  width:1.25rem;
  margin-left:.5vh;
}
.anyManaSymbol{
  background-color: #cac5c0;
  border-radius: 50%;
  padding-left: 0.4rem;
  padding-right:0.4rem;
  font-size:.8rem;
}



